.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 700px; /* Если нужно немного отступа сверху */
  margin-left: 40px;
}

.nav-link {
  margin: 0 15px; /* Равные отступы между ссылками */
  text-decoration: none;
  color: #333;
  font-size: 18px;
  font-weight: bold;
}

.nav-link:hover {
  color: #007bff; /* Цвет ссылки при наведении */
}
